import React, { PureComponent } from 'react'
import Form, { IProps } from './Form'
import FormGenerator from './FormGenerator'
import { STOCK_ITEMS, STOCK_SUPPLIERS, STOCK_TRANSACTIONS } from '../../graphql/query/remote'
import Query from '../Query'
import { CREATE_STOCK_TRANSACTION } from '../../graphql/mutation/remote'

class CreateStockTransaction extends PureComponent<IProps> {
  render() {
    const { id } = this.props.match.params
    return (

      <Query query={STOCK_SUPPLIERS} variables={{ limit: 0 }}>
        {({ loading, error, data: { suppliers } }: IGraphqlData) => {
          return (
            <Query query={STOCK_ITEMS} variables={{ limit: 0 }}>
              {({ loading, error, data: { stocks } }: IGraphqlData) => {
                if (loading) {
                  return (<span>loading...</span>)
                } else if (error) {
                  return (<span>Error Occured!</span>)
                }
                const defaults: IFormGenerator = {
                  stockId: {
                    label: 'stock',
                    type: 'select',
                    default: stocks.edges,
                    mapping: { key: 'name', value: 'id' },
                    required: true,
                  },
                  supplierId: {
                    label: 'supplier',
                    type: 'select',
                    default: suppliers.edges,
                    mapping: { key: 'name', value: 'id' },
                    required: true,
                  },
                  type: {
                    type: 'select',
                    default: [{
                      key: 'CREDIT [+]',
                      value: 'CREDIT',
                    },
                    {
                      key: 'DEBIT [-]',
                      value: 'DEBIT',
                    }],
                    mapping: {key: 'key', value: 'value'},
                    required: true,
                  },
                  quantity: {
                    type: 'number',
                    default: 0,
                    min: 1,
                    required: true,
                  },
                }
                return (
                  <FormGenerator
                    {...this.props}
                    defaults={defaults}
                    mutation={CREATE_STOCK_TRANSACTION}
                    title={'Create a new Stock Transaction'}
                    submitText={'Create StockTransaction'}
                    mutateFunc={(fn, data) => fn({ variables: { data } })}
                  >
                  </FormGenerator>
                )
              }}
            </Query>
          )
        }}
      </Query>
    )
  }
}

export default CreateStockTransaction
